import React, { createContext, useContext } from "react";

export const FHIRDataContext = createContext(null);

export const FHIRDataProvider = ({ value, children }) => (
  <FHIRDataContext.Provider value={value}>{children}</FHIRDataContext.Provider>
);

export const useFHIR = () => {
  const ctx = useContext(FHIRDataContext);
  if (!ctx) {
    throw new Error("useFHIR must be used within FHIRDataProvider");
  }
  return ctx;
};
