import { useEffect, useState, useCallback } from "react";
import FHIR from "fhirclient";

export default function useFHIRData() {
  const [patient, setPatient] = useState(null);
  const [doctor, setDoctor] = useState(null);
  const [medications, setMedications] = useState([]);
  const [carePlans, setCarePlans] = useState([]);
  const [appointments, setAppointments] = useState([]);
  const [risks, setRisks] = useState([]);
  const [reminders, setReminders] = useState([]);
  const [err, setErr] = useState(null);
  const [client, setClient] = useState(null);
  const [medicationRecords, setMedicationRecords] = useState({});
  const [encounters, setEncounters] = useState([]);     // 就诊/住院记录
  const [conditions, setConditions] = useState([]);      // 病史记录
  const [doseTakenByDose, setDoseTakenByDose] = useState({}); // 每剂次打卡：{medKey:{dateKey:[bool]}}

  const today = new Date().toISOString().split("T")[0];
  const storageKey = patient ? `medrecs:${patient.id}` : null;
  const doseStoreKey = patient ? `medTakenByDose:${patient.id}` : null;
  const getMedKey = (m) => (
    m?.id || m?.medicationCodeableConcept?.text || m?.medicationReference?.reference || '__unknown__'
  );

  // 患者端：按剂次打卡
  const markDoseTaken = (medIndex, dateKey, doseIndex, freq) => {
    const med = medications[medIndex];
    if (!med) return;
    const mKey = getMedKey(med);
    setDoseTakenByDose(prev => {
      const next = { ...prev };
      const byMed = { ...(next[mKey] || {}) };
      const base = Array.isArray(byMed[dateKey]) ? [...byMed[dateKey]] : Array(freq).fill(false);
      base[doseIndex] = true;
      byMed[dateKey] = base;
      next[mKey] = byMed;
      try { if (doseStoreKey) localStorage.setItem(doseStoreKey, JSON.stringify(next)); } catch {}
      return next;
    });

    // 立即更新当前 medications 中的 takenRecords，驱动患者端按钮变为已点击状态
    setMedications(prev => {
      const arr = [...prev];
      const m = arr[medIndex];
      if (!m) return prev;
      const f = freq || (m?.dosageInstruction?.[0]?.timing?.repeat?.frequency || 1);
      m.takenRecords = m.takenRecords ? { ...m.takenRecords } : {};
      const todayArr = Array.isArray(m.takenRecords[dateKey]) ? [...m.takenRecords[dateKey]] : Array(f).fill(false);
      todayArr[doseIndex] = true;
      m.takenRecords[dateKey] = todayArr;
      arr[medIndex] = { ...m };
      return arr;
    });

    // 同步到医生端可见的 edits 存储，触发医生端合并（无需刷新即可监听 storage 时生效）
    try {
      const storeKey = `medEdits:${patient?.id}`;
      const curr = JSON.parse(localStorage.getItem(storeKey) || '{}');
      const k = mKey;
      const rec = curr[k] ? { ...curr[k] } : {};
      // 确保 scheduledTimes 结构存在，医生端即可读取
      rec.scheduledTimes = rec.scheduledTimes || {};
      const arr = Array.isArray(rec.scheduledTimes[dateKey]) ? [...rec.scheduledTimes[dateKey]] : [];
      if (arr.length <= doseIndex) {
        for (let i = arr.length; i <= doseIndex; i++) arr[i] = '';
      }
      curr[k] = rec;
      localStorage.setItem(storeKey, JSON.stringify(curr));
    } catch {}
  };

  const takeMedication = useCallback(
    (dateKey, medName) => {
      setMedicationRecords(prev => {
        const next = { ...prev };
        const day = { ...(next[dateKey] || {}) };
        day[medName] = true;
        next[dateKey] = day;
        if (storageKey) {
          try {
            localStorage.setItem(storageKey, JSON.stringify(next));
          } catch {}
        }
        return next;
      });
    },
    [storageKey]
  );

  
  useEffect(() => {
    if (!storageKey) return;
    try {
      const saved = JSON.parse(localStorage.getItem(storageKey) || "{}");
      setMedicationRecords(saved);
    } catch {}
    const onStorage = e => {
      if (e.key === storageKey) {
        try {
          setMedicationRecords(JSON.parse(e.newValue || "{}"));
        } catch {}
      }
    };
    window.addEventListener("storage", onStorage);
    return () => window.removeEventListener("storage", onStorage);
  }, [storageKey]);

  // 加载每剂次打卡与监听
  useEffect(() => {
    if (!doseStoreKey) return;
    try {
      const saved = JSON.parse(localStorage.getItem(doseStoreKey) || '{}');
      setDoseTakenByDose(saved || {});
    } catch {}
    const onStorage = (e) => {
      if (e.key === doseStoreKey) {
        try { setDoseTakenByDose(JSON.parse(e.newValue || '{}') || {}); } catch {}
      }
    };
    window.addEventListener('storage', onStorage);
    return () => window.removeEventListener('storage', onStorage);
  }, [doseStoreKey]);

  useEffect(() => {
    FHIR.oauth2.ready().then(async c => {
      setClient(c);
      try {
        let p = null;
        try {
          p = await c.patient.read();
          setPatient(p);
        } catch {}

        if (p?.generalPractitioner?.length > 0) {
          const gpRef = p.generalPractitioner[0].reference;
          const gp = await c.request(gpRef);
          setDoctor(gp);
        } else if (p?.id) {
          const encForGp = await c.request(`Encounter?patient=${p.id}`);
          if (encForGp.entry && encForGp.entry.length > 0) {
            const practitionerRef =
              encForGp.entry[0].resource?.participant?.[0]?.individual?.reference;
            if (practitionerRef) {
              const gp = await c.request(practitionerRef);
              setDoctor(gp);
            }
          }
        }

        const meds = await c.request(`MedicationRequest?patient=${p?.id}`);
        const medList = meds.entry ? meds.entry.map(e => ({ ...e.resource })) : [];
        setMedications(medList);

        // 合并医生端编辑（标签/排程）
        try {
          const editsStore = JSON.parse(localStorage.getItem(`medEdits:${p?.id}`) || '{}');
          if (editsStore && Object.keys(editsStore).length) {
            const merged = medList.map((m) => {
              const k = (m?.id || m?.medicationCodeableConcept?.text || m?.medicationReference?.reference || `med-${m?.id || ''}`);
              const e = editsStore[k];
              if (!e) return m;
              const next = { ...m };
              if (e.doseLabels) next.doseLabels = e.doseLabels;
              if (e.scheduledTimes) next.scheduledTimes = e.scheduledTimes;
              return next;
            });
            setMedications(merged);
            // 将合并后的列表继续用于后续 merge
            medList.splice(0, medList.length, ...merged);
          }
        } catch {}

        // 合并患者端按剂次打卡（跨标签/跨视图）
        try {
          const doseStore = JSON.parse(localStorage.getItem(`medTakenByDose:${p?.id}`) || '{}');
          if (doseStore && Object.keys(doseStore).length) {
            const merged2 = medList.map((m) => {
              const k = (m?.id || m?.medicationCodeableConcept?.text || m?.medicationReference?.reference || `med-${m?.id || ''}`);
              const perMed = doseStore[k];
              if (!perMed) return m;
              const next = { ...m };
              next.takenRecords = next.takenRecords ? { ...next.takenRecords } : {};
              Object.entries(perMed).forEach(([dKey, arr]) => {
                const f = Array.isArray(arr) ? arr.length : 0;
                const base = Array.isArray(next.takenRecords[dKey]) ? [...next.takenRecords[dKey]] : Array(f).fill(false);
                for (let i = 0; i < f; i++) base[i] = Boolean(base[i] || arr[i]);
                next.takenRecords[dKey] = base;
              });
              return next;
            });
            setMedications(merged2);
          }
        } catch {}

        const plans = await c.request(`CarePlan?patient=${p?.id}`);
        const planList = plans.entry ? plans.entry.map(e => e.resource) : [];
        setCarePlans(planList);

        const appts = await c.request(`Appointment?patient=${p?.id}`);
        const apptList = appts.entry ? appts.entry.map(e => e.resource) : [];
        setAppointments(apptList);

        const detectedRisks = medList
          .filter(m => m.status === "stopped")
          .map(m => `Medication ${m.medicationCodeableConcept?.text || "Unknown"} was stopped`);
        setRisks(detectedRisks);

        // Encounters（就诊/住院记录）
        try {
          const encRes = await c.request(`Encounter?patient=${p?.id}`);
          const encList = encRes.entry ? encRes.entry.map(e => e.resource) : [];
          const normalizedEncounters = encList.map(en => ({
            id: en.id,
            date: en.period?.start || en.actualPeriod?.start || en.meta?.lastUpdated || null,
            category: en.class?.display || en.class?.code || en.type?.[0]?.text || 'N/A',
            hhs: en.serviceProvider?.display || en.serviceProvider?.reference || 'N/A',
            reason: en.reasonCode?.[0]?.text || en.reasonReference?.[0]?.display || 'N/A',
          }));
          setEncounters(normalizedEncounters);
          try {
            const localEnc = JSON.parse(localStorage.getItem(`encounters:${p?.id}`) || '[]');
            if (Array.isArray(localEnc) && localEnc.length) {
              setEncounters(prev => [...localEnc, ...prev]);
            }
          } catch {}
        } catch {}

        // Conditions（病史）
        try {
          const condRes = await c.request(`Condition?patient=${p?.id}`);
          const condList = condRes.entry ? condRes.entry.map(e => e.resource) : [];
          const normalizedConditions = condList.map(cd => ({
            id: cd.id,
            name: cd.code?.text || cd.code?.coding?.[0]?.display || 'Unknown',
            onset: cd.onsetDateTime || cd.onsetPeriod?.start || cd.recordedDate || null,
          }));
          setConditions(normalizedConditions);
          try {
            const localCon = JSON.parse(localStorage.getItem(`conditions:${p?.id}`) || '[]');
            if (Array.isArray(localCon) && localCon.length) {
              setConditions(prev => [...localCon, ...prev]);
            }
          } catch {}
        } catch {}

        // Reminders（随访提醒）
        try {
          const savedRem = JSON.parse(localStorage.getItem(`reminders:${p?.id}`) || '[]');
          if (Array.isArray(savedRem) && savedRem.length) setReminders(savedRem);
        } catch {}
      } catch (e) {
        setErr(e);
      }
    });
  }, []);

  // 监听跨标签变更：医生端编辑、患者端按剂次打卡
  useEffect(() => {
    if (!patient?.id) return;
    const onStorage = (ev) => {
      const key = ev.key || '';
      const pid = patient.id;
      if (key === `medEdits:${pid}` || key === `medTakenByDose:${pid}`) {
        try {
          setMedications((prev) => {
            const list = [...prev];
            if (key === `medEdits:${pid}`) {
              const store = JSON.parse(ev.newValue || '{}');
              return list.map((m) => {
                const k = (m?.id || m?.medicationCodeableConcept?.text || m?.medicationReference?.reference || '');
                const e = store[k];
                if (!e) return m;
                const next = { ...m };
                if (e.doseLabels) next.doseLabels = e.doseLabels;
                if (e.scheduledTimes) next.scheduledTimes = e.scheduledTimes;
                return next;
              });
            }
            if (key === `medTakenByDose:${pid}`) {
              const store = JSON.parse(ev.newValue || '{}');
              return list.map((m) => {
                const k = (m?.id || m?.medicationCodeableConcept?.text || m?.medicationReference?.reference || '');
                const perMed = store[k];
                if (!perMed) return m;
                const next = { ...m };
                next.takenRecords = next.takenRecords ? { ...next.takenRecords } : {};
                Object.entries(perMed).forEach(([dKey, arr]) => {
                  const base = Array.isArray(next.takenRecords[dKey]) ? [...next.takenRecords[dKey]] : Array(arr.length).fill(false);
                  for (let i = 0; i < arr.length; i++) base[i] = Boolean(base[i] || arr[i]);
                  next.takenRecords[dKey] = base;
                });
                return next;
              });
            }
            return list;
          });
        } catch {}
      }
    };
    window.addEventListener('storage', onStorage);
    return () => window.removeEventListener('storage', onStorage);
  }, [patient?.id]);

  // 本地新增/编辑方法（仅前端持久化）
  const addEncounterLocal = (record) => {
    setEncounters(prev => {
      const next = [{ ...record, id: `local-${Date.now()}` }, ...prev];
      try {
        localStorage.setItem(`encounters:${patient?.id}`,
          JSON.stringify(next.filter(x => String(x.id).startsWith('local-'))));
      } catch {}
      return next;
    });
  };

  const addConditionLocal = (name, onset) => {
    setConditions(prev => {
      const next = [{ id: `local-${Date.now()}`, name, onset }, ...prev];
      try {
        localStorage.setItem(`conditions:${patient?.id}`,
          JSON.stringify(next.filter(x => String(x.id).startsWith('local-'))));
      } catch {}
      return next;
    });
  };

  const addFollowUpReminder = (text, dateTimeIso) => {
    setReminders(prev => {
      const next = [...prev, `${text}${dateTimeIso ? ' — ' + new Date(dateTimeIso).toLocaleString() : ''}`];
      try { localStorage.setItem(`reminders:${patient?.id}`, JSON.stringify(next)); } catch {}
      return next;
    });
  };

  // 医生端：剂量标签与排程
  const updateDoseLabel = (medIndex, doseIndex, label) => {
    setMedications(prev => {
      const next = [...prev];
      const m = next[medIndex];
      const freq = m?.dosageInstruction?.[0]?.timing?.repeat?.frequency || 1;
      m.doseLabels = Array.isArray(m.doseLabels) ? [...m.doseLabels] : Array(freq).fill(null);
      m.doseLabels[doseIndex] = label;
      // 持久化到本地，供另一端合并
      try {
        const k = (m?.id || m?.medicationCodeableConcept?.text || m?.medicationReference?.reference || `med-${medIndex}`);
        const storeKey = `medEdits:${patient?.id}`;
        const curr = JSON.parse(localStorage.getItem(storeKey) || '{}');
        const rec = curr[k] ? { ...curr[k] } : {};
        rec.doseLabels = m.doseLabels;
        curr[k] = rec;
        localStorage.setItem(storeKey, JSON.stringify(curr));
      } catch {}
      return next;
    });
  };

  const setDoseSchedule = (medIndex, dateKey, doseIndex, isoString) => {
    setMedications(prev => {
      const next = [...prev];
      const m = next[medIndex];
      m.scheduledTimes = m.scheduledTimes ? { ...m.scheduledTimes } : {};
      const arr = Array.isArray(m.scheduledTimes[dateKey]) ? [...m.scheduledTimes[dateKey]] : [];
      arr[doseIndex] = isoString;
      m.scheduledTimes[dateKey] = arr;
      try {
        const k = (m?.id || m?.medicationCodeableConcept?.text || m?.medicationReference?.reference || `med-${medIndex}`);
        const storeKey = `medEdits:${patient?.id}`;
        const curr = JSON.parse(localStorage.getItem(storeKey) || '{}');
        const rec = curr[k] ? { ...curr[k] } : {};
        rec.scheduledTimes = m.scheduledTimes;
        curr[k] = rec;
        localStorage.setItem(storeKey, JSON.stringify(curr));
      } catch {}
      return next;
    });
  };

  return {
    patient,
    doctor,
    medications,
    carePlans,
    appointments,
    risks,
    reminders,
    today,
    err,
    setMedications,
    client,
    medicationRecords,
    takeMedication,
    // 剂次交互
    doseTakenByDose,
    markDoseTaken,
    // A/B/C 扩展
    encounters,
    conditions,
    addEncounterLocal,
    addConditionLocal,
    addFollowUpReminder,
    updateDoseLabel,
    setDoseSchedule,
  };
}
