import React, { useState } from "react";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { useFHIR, FHIRDataContext } from "../context/FHIRDataContext.js";
import { formatHumanName } from "../utils/formatHumanName";

const DoctorView = () => {
  const {
    patient,
    medications = [],
    carePlans = [],
    appointments = [],
    risks = [],
    medicationRecords = {},
    encounters = [],
    updateDoseLabel,
    setDoseSchedule,
  } = useFHIR();


  const [selectedDate, setSelectedDate] = useState(new Date());

  if (!patient) return <p>Loading patient data...</p>;

  // 保留原始索引，避免编辑时索引错位
  const uniqueMedications = medications.reduce((acc, med, originalIndex) => {
    const key =
      med.medicationCodeableConcept?.text ||
      med.medicationReference?.reference ||
      "Unknown";
    if (!acc.some((x) => x.key === key)) {
      acc.push({ key, med, originalIndex });
    }
    return acc;
  }, []);

  const dateKey = selectedDate.toISOString().split("T")[0];
  const todayKey = new Date().toISOString().split("T")[0];

  return (
    <div>
      <h2>👨‍⚕️ Doctor View</h2>

      <section style={{ marginTop: "20px" }}>
        <h3>👤 Patient Info</h3>
        <p>
          Name: {patient?.name ? formatHumanName(patient.name) : "Unknown"}
        </p>
        <p>Gender: {patient?.gender || "Unknown"}</p>
        <p>Birth Date: {patient?.birthDate || "Unknown"}</p>
        <p>ID: {patient?.id || "Unknown"}</p>
        <p>
          Address: {patient?.address?.[0]?.text || [
            patient?.address?.[0]?.line?.join(' '),
            patient?.address?.[0]?.city,
            patient?.address?.[0]?.state,
            patient?.address?.[0]?.postalCode,
            patient?.address?.[0]?.country,
          ].filter(Boolean).join(', ') || 'Unknown'}
        </p>
        <p>Medical Center: {encounters?.[0]?.hhs || "Unknown"}</p>
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>📅 Medication Records</h3>
        <Calendar
          locale="en-US"
          onChange={setSelectedDate}
          value={selectedDate}
          tileClassName={({ date }) => {
            const key = date.toISOString().split("T")[0];
            const anyTaken = medications.some((m) =>
              Array.isArray(m.takenRecords?.[key]) && m.takenRecords[key].some(Boolean)
            );
            return anyTaken ? "bg-green-200" : null;
          }}
        />
        <div style={{ marginTop: "10px" }}>
          <strong>{selectedDate.toDateString()}</strong>
          {uniqueMedications.length === 0 ? (
            <p>No medications found.</p>
          ) : (
            uniqueMedications.map((item) => {
              const med = item.med;
              const medName = med.medicationCodeableConcept?.text || med.medicationReference?.reference || "Unknown";
              const freq = med.dosageInstruction?.[0]?.timing?.repeat?.frequency || 1;
              const arr = Array.isArray(med.takenRecords?.[dateKey]) ? med.takenRecords[dateKey] : Array(freq).fill(false);
              const cnt = arr.filter(Boolean).length;
              return (
                <div key={item.key} style={{ marginBottom: "6px" }}>
                  {medName}: {cnt > 0 ? "✔ Taken" : "❌ Not taken"}
                </div>
              );
            })
          )}
        </div>
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>💊 Medications</h3>
        {uniqueMedications.length === 0 ? (
          <p>No medications found.</p>
        ) : (
          uniqueMedications.map((item) => {
            const med = item.med;
            const index = item.originalIndex;
            const freq = med.dosageInstruction?.[0]?.timing?.repeat?.frequency || 1;
            const labels = Array.isArray(med.doseLabels) ? med.doseLabels : Array(freq).fill(null);
            return (
              <div key={item.key} style={{ marginBottom: "10px" }}>
                <p>
                  <strong>Name:</strong>{" "}
                  {med.medicationCodeableConcept?.text || "Unknown"}
                </p>
                <p>
                  <strong>Status:</strong> {med.status}
                </p>
                <p>
                  <strong>Intent:</strong> {med.intent}
                </p>

                {/* 剂量标签编辑 */}
                <details style={{ marginTop: 6 }}>
                  <summary>Edit dose labels</summary>
                  <div style={{ marginTop: 6 }}>
                    {Array.from({ length: freq }).map((_, i) => (
                      <div key={i} style={{ marginBottom: 6 }}>
                        <label>Label for Dose {i+1}: </label>
                        <input
                          defaultValue={labels[i] || ""}
                          onBlur={(e) => updateDoseLabel(index, i, e.target.value)}
                          style={{ border: "1px solid #ddd", padding: "2px 6px", borderRadius: 4 }}
                        />
                      </div>
                    ))}
                  </div>
                </details>

                {/* 指定日期时间 */}
                <details style={{ marginTop: 6 }}>
                  <summary>Schedule dose date/time</summary>
                  <div style={{ marginTop: 6 }}>
                    {Array.from({ length: freq }).map((_, i) => {
                      const mKey = (med?.id || med?.medicationCodeableConcept?.text || med?.medicationReference?.reference || `med-${index}`);
                      const takenArr = med.scheduledTimes?.[todayKey] || med.scheduledTimes?.[dateKey] || [];
                      // 如果患者点击了 dose i，我们在患者端已持久化；这里基于 takenByDose 判断是否显示
                      // 简化：若当天 dose i 已在 takenRecords 中为 true 或已设置过时间，则显示
                      const clicked = Array.isArray(med.takenRecords?.[todayKey]) ? med.takenRecords[todayKey][i] : false;
                      const prefilled = takenArr[i] || "";
                      if (!clicked && !prefilled) return null;
                      return (
                        <div key={i} style={{ marginBottom: 6 }}>
                          <label>Dose {i+1} at: </label>
                          <input
                            type="datetime-local"
                            defaultValue={prefilled}
                            onChange={(e) => setDoseSchedule(index, todayKey, i, e.target.value)}
                            style={{ border: "1px solid #ddd", padding: "2px 6px", borderRadius: 4 }}
                          />
                        </div>
                      );
                    })}
                  </div>
                </details>
              </div>
            );
          })
        )}
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>📝 Care Plans</h3>
        {carePlans.length === 0 ? (
          <p>No care plans.</p>
        ) : (
          carePlans.map((plan, index) => (
            <div
              key={index}
              style={{
                border: "1px solid #ddd",
                borderRadius: "8px",
                padding: "10px",
                marginBottom: "10px",
                backgroundColor: "#f9f9f9",
              }}
            >
              <p>
                <strong>Category:</strong> {plan.category?.[0]?.text || "Unknown"}
              </p>
              <p>
                <strong>Status:</strong> {plan.status || "Unknown"}
              </p>
              <p>
                <strong>Period:</strong>{" "}
                {plan.period?.start
                  ? new Date(plan.period.start).toLocaleDateString()
                  : "Unknown"}{" "}
                -{" "}
                {plan.period?.end
                  ? new Date(plan.period.end).toLocaleDateString()
                  : "Ongoing"}
              </p>

              <p>
                <strong>Activities:</strong>
              </p>
              {plan.activity && plan.activity.length > 0 ? (
                plan.activity.map((act, i) => {
                  const description =
                    act.detail?.description ||
                    act.detail?.code?.text ||
                    act.outcomeCodeableConcept?.[0]?.text ||
                    "No description";
                  const status = act.detail?.status || "unknown";
                  return (
                    <div key={i} style={{ marginLeft: "10px" }}>
                      {description} ({status})
                    </div>
                  );
                })
              ) : (
                <p>No activities listed</p>
              )}
            </div>
          ))
        )}
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>📅 Appointments</h3>
        {appointments.length === 0 ? (
          <p>No appointments.</p>
        ) : (
          appointments
            .filter((appt) => appt.start && new Date(appt.start) >= new Date()) 
            .sort((a, b) => new Date(a.start) - new Date(b.start)) 
            .map((appt, index) => (
              <div key={index} style={{ marginBottom: "10px" }}>
                {appt.start
                  ? new Date(appt.start).toLocaleString()
                  : "Unknown"} — {appt.description || "Follow-up"}
              </div>
            ))
        )}
      </section>



      <section style={{ marginTop: "20px" }}>
        <h3>⚠️ Risks</h3>
        {risks.length === 0 ? (
          <p>No risks identified.</p>
        ) : (
          [...new Set(risks)].map((risk, index) => (
            <div key={index} style={{ marginBottom: "10px" }}>
              {risk}
            </div>
          ))
        )}
      </section>
    </div>
  );
};

export default DoctorView;
