import React, { useEffect, useState } from "react";
import DoctorView from "./components/DoctorView";
import PatientView from "./components/PatientView";
import useFHIRData from "./hooks/useFHIRData";
import { FHIRDataProvider, useFHIR } from "./context/FHIRDataContext.js";

function RoleGate() {
  const { client, err } = useFHIR();
  const [role, setRole] = useState(null);
  const [consentGiven, setConsentGiven] = useState(false);

  useEffect(() => {
    if (!client) return;
    if (client.user && client.user.fhirUser?.includes("Practitioner")) {
      setRole("doctor");
    } else if (client.patient?.id) {
      setRole("patient");
    }
  }, [client]);

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">Emergency Intervention System</h1>

      {err && <p className="text-red-600">Error: {String(err)}</p>}

      {role === "doctor" && <DoctorView />}

      {role === "patient" && (
        !consentGiven ? (
          <div className="bg-yellow-50 p-4 rounded shadow">
            <h2 className="text-lg font-semibold mb-2">Consent Required</h2>
            <p className="mb-4">
              To continue, please consent to the analysis and use of your health data.
            </p>
            <button
              onClick={() => setConsentGiven(true)}
              className="px-4 py-2 bg-green-600 text-white rounded"
            >
              I Consent
            </button>
          </div>
        ) : (
          <PatientView />
        )
      )}
    </div>
  );
}

export default function App() {
  const data = useFHIRData();

  return (
    <FHIRDataProvider value={data}>
      <RoleGate />
    </FHIRDataProvider>
  );
}
