export function formatHumanName(names) {
  if (!Array.isArray(names) || names.length === 0) return "Unknown";
  const n = names[0];

  const prefix = Array.isArray(n.prefix) ? n.prefix.join(" ") : (n.prefix || "");
  const given = Array.isArray(n.given) ? n.given.join(" ") : (n.given || "");
  const family = n.family || "";

  return [prefix, given, family].filter(Boolean).join(" ") || "Unknown";
}
