import React, { useState } from "react";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { useFHIR, FHIRDataContext } from "../context/FHIRDataContext.js";
import { formatHumanName } from "../utils/formatHumanName";

const PatientView = () => {
  const {
    patient,
    doctor,
    medications = [],
    carePlans = [],
    appointments = [],
    risks = [],
    reminders = [],
    medicationRecords = {},
    takeMedication,
    // A/C: encounters, conditions, adders
    encounters,
    addEncounterLocal,
    conditions,
    addConditionLocal,
    addFollowUpReminder,
    // 剂次交互
    markDoseTaken,
  } = useFHIR();

  const [consentGiven, setConsentGiven] = useState(false);
  const [selectedDate, setSelectedDate] = useState(new Date());

  if (!patient) return <p>Loading patient data...</p>;

  // 保留原始索引，避免点击时索引错位
  const uniqueMedications = medications.reduce((acc, med, originalIndex) => {
    const key =
      med.medicationCodeableConcept?.text ||
      med.medicationReference?.reference ||
      "Unknown";
    if (!acc.some((x) => x.key === key)) {
      acc.push({ key, med, originalIndex });
    }
    return acc;
  }, []);

  if (!consentGiven) {
    setConsentGiven(true);
    return <p>Loading your data...</p>;
  }

  const dateKey = selectedDate.toISOString().split("T")[0];

  return (
    <div>
      <h2>👤 Patient View</h2>

      {/* Encounters */}
      <section style={{ marginTop: "20px" }}>
        <h3>🏥 Emergency/Inpatient Records</h3>
        {encounters?.length === 0 ? (
          <p>No records.</p>
        ) : (
          <table style={{ width: '100%', borderCollapse: 'collapse' }}>
            <thead>
              <tr>
                <th style={{ textAlign: 'left' }}>Date</th>
                <th style={{ textAlign: 'left' }}>Triage/Category</th>
                <th style={{ textAlign: 'left' }}>HHS / Service</th>
                <th style={{ textAlign: 'left' }}>Reason</th>
              </tr>
            </thead>
            <tbody>
              {encounters.map(r => (
                <tr key={r.id}>
                  <td>{r.date ? new Date(r.date).toLocaleString() : 'N/A'}</td>
                  <td>{r.category}</td>
                  <td>{r.hhs}</td>
                  <td>{r.reason}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
        <div style={{ marginTop: 10 }}>
          <button
            onClick={() => {
              const date = prompt("Date/Time (ISO or text):");
              const category = prompt("Triage/Category:");
              const hhs = prompt("HHS / Service:");
              const reason = prompt("Reason:");
              if (date || category || hhs || reason) {
                addEncounterLocal({ date, category, hhs, reason });
              }
            }}
            className="px-3 py-1 rounded bg-blue-600 text-white"
          >
            + Add Encounter
          </button>
        </div>
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>🩺 My General Practitioner</h3>
        {doctor ? (
          <div>
            <p>
              <strong>Name:</strong>{" "}
              {doctor.name?.length ? formatHumanName(doctor.name) : "Unknown"}
            </p>
            <p>
              <strong>Address:</strong>{" "}
              {doctor.address?.[0]
                ? [
                    doctor.address[0].line?.join(", "),
                    doctor.address[0].city,
                    doctor.address[0].state,
                    doctor.address[0].postalCode,
                    doctor.address[0].country,
                  ]
                    .filter(Boolean)
                    .join(", ")
                : "Unknown"}
            </p>
          </div>
        ) : (
          <p>No GP information available.</p>
        )}
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>📋 My Medications</h3>
        {uniqueMedications.length === 0 ? (
          <p>No medications found.</p>
        ) : (
          uniqueMedications.map((med, index) => (
            <div
              key={index}
              style={{
                border: "1px solid #ddd",
                borderRadius: "8px",
                padding: "10px",
                marginBottom: "10px",
                backgroundColor: "#f6fff6",
              }}
            >
              <p>
                <strong>Medication:</strong>{" "}
                {med.medicationCodeableConcept?.text || "Unknown"}
              </p>
              <p>
                <strong>How to take:</strong>{" "}
                {med.dosageInstruction?.[0]?.text || "Follow doctor's advice"}
              </p>
            </div>
          ))
        )}
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>📅 Medication Calendar</h3>
        <Calendar locale="en-US" onChange={setSelectedDate} value={selectedDate} />
        <div style={{ marginTop: "10px" }}>
          <strong>{selectedDate.toDateString()}</strong>
          {uniqueMedications.length === 0 ? (
            <p>No medications found.</p>
          ) : (
            uniqueMedications.map((item) => {
              const med = item.med;
              const idx = item.originalIndex;
              const medName = med.medicationCodeableConcept?.text || med.medicationReference?.reference || "Unknown";
              const freq = med.dosageInstruction?.[0]?.timing?.repeat?.frequency || 1;
              const todayKey = dateKey;
              const todayRecords = Array.isArray(med.takenRecords?.[todayKey])
                ? med.takenRecords[todayKey]
                : Array(freq).fill(false);
              return (
                <div key={item.key} style={{ marginBottom: "10px" }}>
                  <div style={{ marginBottom: "4px" }}>
                    <strong>{medName}</strong> — Today Taken: {todayRecords.filter(Boolean).length}/{freq}
                  </div>
                  <div style={{ display: "flex", gap: "8px" }}>
                    {Array.from({ length: freq }).map((_, i) => (
                      <button
                        key={i}
                        onClick={() => {
                          // 原有总览打卡（保持）
                          takeMedication(todayKey, medName);
                          // 新增：记录具体剂次
                          markDoseTaken(idx, todayKey, i, freq);
                        }}
                        disabled={!!todayRecords[i]}
                        style={{
                          padding: "2px 8px",
                          borderRadius: "4px",
                          border: "none",
                          backgroundColor: todayRecords[i] ? "#9ca3af" : "#16a34a",
                          color: todayRecords[i] ? "#111827" : "#ffffff",
                          cursor: todayRecords[i] ? "not-allowed" : "pointer",
                        }}
                      >
                        {todayRecords[i] ? "✅" : `Dose ${i + 1}`}
                      </button>
                    ))}
                  </div>
                </div>
              );
            })
          )}
        </div>
      </section>

      {/* Medical History */}
      <section style={{ marginTop: "20px" }}>
        <h3>🩺 Medical History</h3>
        {conditions?.length === 0 ? (
          <p>No records.</p>
        ) : (
          <ul>
            {conditions.map(c => (
              <li key={c.id}>{c.name} — {c.onset ? new Date(c.onset).toLocaleDateString() : 'N/A'}</li>
            ))}
          </ul>
        )}
        <div style={{ marginTop: 8 }}>
          <button
            onClick={() => {
              const name = prompt("Condition name:");
              const when = prompt("Onset date (YYYY-MM-DD) optional:");
              if (name) addConditionLocal(name, when || null);
            }}
            className="px-3 py-1 rounded bg-blue-600 text-white"
          >
            + Add Condition
          </button>
        </div>
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>📝 My Care Plans</h3>
        {carePlans.length === 0 ? (
          <p>No care plans found.</p>
        ) : (
          carePlans.map((plan, index) => (
            <div
              key={index}
              style={{
                border: "1px solid #ddd",
                borderRadius: "8px",
                padding: "10px",
                marginBottom: "10px",
                backgroundColor: "#f9f9f9",
              }}
            >
              <p>
                <strong>Category:</strong> {plan.category?.[0]?.text || "Unknown"}
              </p>
              <p>
                <strong>Status:</strong> {plan.status || "Unknown"}
              </p>
              <p>
                <strong>Period:</strong>{" "}
                {plan.period?.start
                  ? new Date(plan.period.start).toLocaleDateString()
                  : "Unknown"}{" "}
                -{" "}
                {plan.period?.end
                  ? new Date(plan.period.end).toLocaleDateString()
                  : "Ongoing"}
              </p>

              <p>
                <strong>Activities:</strong>
              </p>
              {plan.activity && plan.activity.length > 0 ? (
                plan.activity.map((act, i) => {
                  const description =
                    act.detail?.description ||
                    act.detail?.code?.text ||
                    act.outcomeCodeableConcept?.[0]?.text ||
                    "No description";
                  const status = act.detail?.status || "unknown";
                  return (
                    <div key={i} style={{ marginLeft: "10px" }}>
                      {description} ({status})
                    </div>
                  );
                })
              ) : (
                <p>No activities listed</p>
              )}
            </div>
          ))
        )}
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>📅 Appointments</h3>
        {appointments.length === 0 ? (
          <p>No appointments.</p>
        ) : (
          appointments
            .filter((appt) => appt.start && new Date(appt.start) >= new Date())
            .sort((a, b) => new Date(a.start) - new Date(b.start))
            .map((appt, index) => (
              <div key={index} style={{ marginBottom: "10px" }}>
                {appt.start
                  ? new Date(appt.start).toLocaleString()
                  : "Unknown"} — {appt.description || "Follow-up"}
              </div>
            ))
        )}
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>⚠️ Risks</h3>
        {risks.length === 0 ? (
          <p>No risks identified.</p>
        ) : (
          [...new Set(risks)].map((risk, index) => (
            <div key={index} style={{ marginBottom: "10px" }}>
              {risk}
            </div>
          ))
        )}
      </section>

      <section style={{ marginTop: "20px" }}>
        <h3>🔔 Reminders</h3>
        <div style={{ margin: '10px 0' }}>
          <button
            onClick={() => {
              const text = prompt("Follow-up test item:");
              const when = prompt("DateTime (yyyy-mm-ddThh:mm) optional:");
              if (text) addFollowUpReminder(text, when || null);
            }}
            className="px-3 py-1 rounded bg-blue-600 text-white"
          >
            + Add Follow-up Reminder
          </button>
        </div>
        {reminders.length === 0 ? (
          <p>No reminders set.</p>
        ) : (
          [...new Set(reminders)].map((reminder, index) => (
            <div key={index} style={{ marginBottom: "10px" }}>
              {reminder}
            </div>
          ))
        )}
      </section>
    </div>
  );
};

export default PatientView;
